@use 'base/mixins' as m;
@use 'base/variables' as v;

.paneles{
    background-color: v.$grisIntermedio;
    .paneles__imagen{
       margin: auto;
    }
}
.paneles-solares__imagen{
    padding-top: 5rem;
}
@include m.tablet{
    .paneles{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        column-gap: 3rem;
    }
}


