@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-contenido{
    background-color: v.$grisIntermedio;
    margin-top: 2rem;
    display: flex;
    flex-direction: column-reverse;
    
    align-items: center;
    gap: 1rem;
   p{
    padding-left: 1rem;
    
   }
   

}
@include m.tablet{
    .contenedor-contenido{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        img{
            padding: 1rem;
           }
        
    }
}

.campo{

    svg{
        width: 10rem;
    }
}
.contenedor-iconos{
    display: flex;
    flex-direction: column;
    h3{
        text-align: center;
    }
    .campo{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    @include m.tablet{
        display: grid;
    grid-template-columns: repeat(2,1fr);
    margin-top: 2rem;
    text-align: center;
    h3{
        text-transform: uppercase;
    }
    column-gap: 3rem;
    }
}



