@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario-contacto{
    
    margin-top: 5rem;
  
    fieldset{
     border-style: solid;
     border-color: v.$blanco;
     border-radius: 1rem;
     background-color: v.$grisIntermedio;
     text-align: center;
     input{
         border-style:none ;
         border-radius: .5rem;
         width: 90%;
         height: 4rem;
         margin: 1rem;
         
     }
     textarea{
         border-style:none ;
         border-radius: .5rem;
         overflow: auto;
         resize: vertical;
         width: 90%;
         margin: 1rem;
         height: 10rem;
     }
     input[type="submit"]{
         cursor: pointer;
         background-color: v.$colorAg;
         color: v.$blanco;
         transition-property: background-color;
         transition-duration: 500ms;
         width: 30%;
        &:hover{
         background-color: darken(v.$colorAg, 30%);
        }
     }
    }
    p{
     color: v.$negro;
     line-height: 2rem;
     font-size: 1rem;
     text-align: justify;
    }

       
     
     
}
.alerta{
    .exito{
        background-color: v.$verde;
        color: v.$blanco;
        text-align: center;
        font-size: 15px;
        margin: 0 1rem 0 1rem;
        padding: 0.5rem;
    }

    .error{
        background-color: v.$rojo;
        color: v.$blanco;
        text-align: center;
        font-size: 15px;
        margin: 0 1rem 0 1rem;
        padding: 0.5rem;
    }
}


 