@use 'base/mixins' as m;
@use 'base/variables' as v;
.header{
   
    background: rgba(0, 0, 0, 0.6) url(../img/principal2.jpg);
    height: 60rem;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
     /**  codigo para dar oscuridad a una imagen**/
    background-blend-mode: darken;
    
    

}
.header__texto{
    position: absolute;
    top: 50%; 
    left: 50%;
    transform: translate(-50%, -50%);
    color: v.$blanco;
    text-align: center;
    
    
    
}
