@use 'base/mixins' as m;
@use 'base/variables' as v;
.pie-pagina{
    background-color: v.$negro;
    margin-top: 5rem;
   }
.pie-pagina__contenedor{
       padding-top: 5rem;
       padding-bottom: 3rem;
       text-align: center;
}
@include m.tablet{
    .pie-pagina__contenedor{
        display: grid;
        grid-template-columns: 2fr 1fr 1fr;
        
    }
}
.pie-pagina__contactos {
    color: v.$blanco;
}
.pie-pagina__contactos h4{
    font-size: 3rem;
    font-weight: bold;
}
.pie-pagina__horarios {
    color: v.$blanco;
}
.pie-pagina__horarios h4 {
    font-size: 3rem;
    font-weight: bold;
}
.logo__pie{
    padding-left: 8.5rem;
}
.logo__pie-pagina{
    color: v.$grisClaro;
    margin-bottom: 2rem;
}
.icon{
    width: 2.5rem;
}
@include m.tablet{
    .pie-pagina__contenedor{
        text-align:unset;
    }
}