@use 'base/mixins' as m;
@use 'base/variables' as v;

.boton-volver {
    display: block;
    text-align: start;
    margin-left: 5rem;
    padding: 0.5rem 0.5rem 0.5rem 2.8rem;
    transition-property: background-color;
    transition-duration: 300ms;
    background-color: lighten(v.$verde, 5%);
    color: v.$blanco;
    width: 10rem;
    border-radius: .5rem;
    font-weight: bold;

    &:hover {
        background-color: v.$verde;
    }
}

.contenedor-crear {
    text-align: center;
    .alerta{
        .error{
            font-weight: bold;
            padding: 1rem ;
        }
        @include m.tablet{
            .error{
                padding: 1rem 20rem;
            }
        }
    }
    
}

.formulario-crearServicio {
    display: inline-block;
    width: 90%;
    
    fieldset {
        background-color: v.$grisIntermedio;
        border-width: 0;
        border-radius: 0.5rem;
        
    }
    input,textarea{
        display: block;
        text-align: start;
    }

    input{
        width: 100%;
        border-radius: .5rem;
        border-width: 0;
        padding: 1rem 0 1rem 0;
       
    }
    textarea{
        resize: vertical;
        overflow: auto;
        width: 100%;
        height: 10rem;
    }
    img{
        margin-left: 5rem;
    }
    @include m.tablet{
        width: 60%;
        input{
            text-align: start;
        }
        img{
            margin-left: 0;
        }
    }

}
.boton-crearServicio{
    input{
        display: flex;
        background-color: v.$colorAg;
        color: v.$blanco;
        font-weight: bold;
        width: 13rem;
        padding-left: auto;
       justify-content: space-around;
        &:hover{
            cursor: pointer;
            background-color: lighten(v.$colorAg, 5%);
        }
    }
   
}