@use 'base/mixins' as m;
@use 'base/variables' as v;

.planos-articulo{
    background-color: v.$grisIntermedio;
    padding-top: 2rem;
    padding-right: 1rem;
    padding-left: 1rem;
}
.planos-electricos__imagen{
    padding-top: 5rem;
}
@include m.tablet{
    .planos-articulo{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        column-gap: 3rem;
        
    }
    .planos-articulo p{
        padding-top: 0rem;
    }
}




