@use 'base/mixins' as m;
@use 'base/variables' as v;

.servicio-contenido{
    text-align: center;
    color: v.$grisTexto;
    h3{
        margin:  4rem 0 0 0;
        color: v.$colorAg;
    }
    h4{
        font-size: large;
        margin: 1rem 0 1rem 0;
        color: v.$colorAg;
}
}
.contenedor-caracteristicas{
    text-align: left;
}
.caracteristicas-servicios{
    display: flex;
    flex-direction: column;
}
.caracteristicas-servicio{
    display: flex;
    align-items: center;
    padding: 1rem 0 1rem 0;
    h5{
        margin: 0;
        font-size: medium;
        color: v.$colorAg;
    }
    svg{
        width: 5rem;
        margin-right: 3rem;
    }
    p{
        margin: 0;
    }
}