@use 'variables' as v;
@use 'base/mixins' as m;

html {
    box-sizing: border-box;
    font-size: 62.5%;
  }
  *, *:before, *:after {
    box-sizing: inherit;
}
body{
    font-family: v.$fuenteHeading;
    font-size: 1.6rem;
    line-height: 2;
    @include m.tablet{
        &.fijo {
            overflow: hidden;
        }
        &.body-scroll {
            padding-top: 8.6rem;
        }
    }
    
    
}

.contenedor{
    width: min(90%, 120rem);
    margin: 0 auto;
}
p{
    font-size: 1.8rem;
}
a{
    text-decoration: none;
    color: inherit;
 }
 img{
    max-width: 100%;
}


