@use 'base/mixins' as m;
@use 'base/variables' as v;

.formulario{
    display: flex;
    flex-direction: column;
   
    .contenedor-contactenos{
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
        svg{
            width: 5rem;
        }
        h3{
            margin: 0;
        }
    }
    
    blockquote{
        border-style: solid;
        border-width: 0 0 0 .5rem;
        border-color: v.$colorAg;
        background-color: v.$grisIntermedio;
        color: v.$grisOscuro;
        margin-left: 0;
        p{
            padding-left: 1rem;
        }
    }
    .iconos-contacto{
        display: flex;
        text-align: center;
        svg{
           
            width: 5rem;
        }
    }
    
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 4rem;
    }
}