@use 'base/mixins' as m;
@use 'base/variables' as v;
.instalaciones-electricas__imagen{
    padding-top: 5rem;
    
}
.entrada{
    background-color: v.$grisIntermedio;
    padding-top: 2rem;
    padding-right: 1rem;
    padding-left: 1rem;
    
    
}
/* media de instalaciones electricas*/
@include m.tablet{
    .entrada{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        column-gap: 8rem;
     }
}