@use 'base/mixins' as m;
@use 'base/variables' as v;

.barra-mobile{
    background-color: v.$blanco;
    padding: 2rem 2rem .5rem 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    h1{
        padding-bottom: 1rem;
    }
    @include m.tablet{
        display:none;
    }
    .menu{
        img{
            width: 4rem;
            margin-left: 1rem;
            &:hover{
                cursor: pointer;
            }
        }
    }
}
.barra{
   display: none;
    background-color: v.$blanco;
    padding: 2rem;
    @include m.tablet{
    
        
        &.fijo{
        position: fixed;
        width: 0%;
        transition-property: width;
        width: 100%;
        box-shadow: .1rem .1rem .1rem #333;
        left: 0;
        top: 0; 
        }
     
    }
    
    &.mostrar {
       display:block;
       h1{
        display: none;
       }
       .navegacion{
        display: flex;
        flex-direction: column;
        align-items: center;
       }
      
    }
   
}
.ocultar-texto{
    display: none;
}

@include m.tablet{
    .barra{
        display:flex;
        justify-content:space-between;
        align-items:center;
         
    }
}
.navegacion{
    display: flex;
    justify-content: space-between;
    gap: 3rem;
}
@include m.tablet{
    .navegacion{
        display: flex;
        gap: 3rem;
    }
    
}
.navegacion__enlace{
    display: block;
    font-size: 1.8rem;
    color: v.$negro;
    padding-left: 2rem;
    padding-right: 2rem;
    font-weight: bold;
}
.navegacion__enlace{
    background-color: v.$blanco;
    border-radius: 2rem;
    transition-property: all;
    transition-duration: 300ms;
    &:hover,
    &.activo{
    border-style: solid;
    border: 100rem;
    border-radius: 2rem;
    background-color:v.$colorAg ;
    color: v.$blanco;
    

}
}

    

.logo__nombre{
    color: v.$grisOscuro;
   
    font-size: 3rem;
   
    @include m.tablet{
        padding-bottom: 0;
    }

}

.logo__ag{
    font-family: v.$fuenteAg;
    color: v.$colorAg;
}