@use 'base/mixins' as m;
@use 'base/variables' as v;
.admin-header{
    background: rgba(0, 0, 0, 0.6) url(../img/login.jpg);
    height: 15rem;
    background-size: cover;
    background-position: center center;
    background-blend-mode: darken;
    h1{
        margin: 0;
        color: v.$blanco;
        text-align: center;
        padding-top: 2rem;
    }
    @include m.tablet{
        height: 10rem;
    }
}
.crear-servicio{
    margin: 2rem 0 2rem 2rem;
   
    a{
    border-radius: .5rem;
    padding: 2rem;
    color: v.$blanco;
    font-weight: bold;
    transition-property: background-color;
    transition-duration: 300ms;
    width: 15%;
    background-color: lighten(v.$verde, 10%);
    &:hover{
        background-color: v.$verde;
    }
   
    }
    @include m.tablet{
        margin: 3rem 0 0 5rem;
        
    }
}
.contenedor-tabla_servicio{
    h2{
        margin: 0;
        text-align: center;
    }
}

.servicios-tabla{
    background-color: v.$gris;
    border-radius: .5rem;
   th{
    background-color: v.$colorAg;
    color: v.$blanco;
   }
    td{
        text-align: center;
        border-style: solid;
        border-width: .1rem;
        border-radius: .5rem;
    }
    @media screen and (max-width: 720px) {
        table{
            width: 100%;
        }
        
        tbody td {
            display: block;
            text-align:center;
        }
        tbody td:before {
            content: attr(data-th);
            display: block;
            text-align:center;
        }
        thead{
            background-color: v.$colorAg;
            color: v.$blanco;
            display: none;
           
        }
    }
    .boton-eliminar{
        color: v.$blanco;
        border-width: 0;
        background-color: v.$rojo;
        transition-property: background-color;
        transition-duration: 300ms;
        padding: .5rem;
        width: 10rem;
        border-radius: .5rem;
        &:hover{
            background-color: darken(v.$rojo, 5%);
            cursor: pointer;
        }
    }
    .boton-actualizar{
        color: v.$blanco;
        background-color: v.$verde;
        transition-property: background-color;
        transition-duration: 300ms;
        padding: .5rem 2rem .5rem 1rem;
        border-radius: .5rem;
        &:hover{
            background-color: darken(v.$verde, 5%);
        }
    }
}
.imagen-tabla{
    width: 20rem;

} 
.alerta{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 1rem;
}
.exito{
    &.admin{
        width: 100rem;
        text-align: center;
        font-weight: bold;
    }
}
//imagen de actualizar
.imagen-small{
    display: flex;
    width: 30rem;
    
}
