@use 'base/mixins' as m;
@use 'base/variables' as v;

.servicio-contenedor{
    display: flex;
    flex-direction: column;
    text-align: center;
    li{
        text-align: start;
    }
    fieldset{
        background-color: rgba(v.$blanco, 25%);
        border-style: unset;
        margin-bottom: 3rem;
        input{
            background-color: rgba(v.$blanco, 80%);
        }
        textarea{
            background-color: rgba(v.$blanco, 80%);
        }
        p{
            color: v.$blanco;
        }
        h3{
            color: v.$blanco;
        }
    }
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        align-items: center;
        fieldset{
            width: 200%;
        }
        
        .formulario-servicio{
            width: 50%;
            padding-bottom: 5rem;
            
            
        }
    }
    
    .descripcion-servicio{
        color: v.$blanco;
        font-size: 2rem;
       p{
        font-size: 2rem;
       }
    }
    
    
}