@use 'base/mixins' as m;
@use 'base/variables' as v;


.servicios-electricos{
    display: flex;
    flex-direction: column;
    margin: 5rem 2rem 0 2rem;
    h3{
        text-align: center;
        margin: 2rem 0 2rem 0;
    }
    img{
        padding-top: 2rem;
    }
    p{
        margin-top: 0;
    }
    @include m.tablet{
    display: grid;
    grid-template-columns: repeat(3,1fr);
   
    column-gap: 2rem;
    background-color: v.$grisIntermedio;
    
    }
    

}