body {
  font-family: "PT Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}
@media (min-width: 768px) {
  body.fijo {
    overflow: hidden;
  }
  body.body-scroll {
    padding-top: 8.6rem;
  }
}

.contenedor {
  width: min(90%, 120rem);
  margin: 0 auto;
}

p {
  font-size: 1.8rem;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "PT Sans", sans-serif;
  line-height: 1.2;
}

h1 {
  font-size: 4.8rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.centrar-texto {
  text-align: center;
}

.margin-top {
  margin-top: 2.5rem;
}

.boton {
  background-color: #052bef;
  color: #FFFFF0;
  padding: 1rem;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  text-align: center;
  flex-direction: column;
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  transition-property: background-color;
  transition-duration: 300ms;
}
.boton:hover {
  background-color: #03198b;
}

.barra-mobile {
  background-color: #FFFFF0;
  padding: 2rem 2rem 0.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.barra-mobile h1 {
  padding-bottom: 1rem;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile .menu img {
  width: 4rem;
  margin-left: 1rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
  background-color: #FFFFF0;
  padding: 2rem;
}
@media (min-width: 768px) {
  .barra.fijo {
    position: fixed;
    width: 0%;
    transition-property: width;
    width: 100%;
    box-shadow: 0.1rem 0.1rem 0.1rem #333;
    left: 0;
    top: 0;
  }
}
.barra.mostrar {
  display: block;
}
.barra.mostrar h1 {
  display: none;
}
.barra.mostrar .navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ocultar-texto {
  display: none;
}

@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.navegacion {
  display: flex;
  justify-content: space-between;
  gap: 3rem;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 3rem;
  }
}
.navegacion__enlace {
  display: block;
  font-size: 1.8rem;
  color: #000;
  padding-left: 2rem;
  padding-right: 2rem;
  font-weight: bold;
}

.navegacion__enlace {
  background-color: #FFFFF0;
  border-radius: 2rem;
  transition-property: all;
  transition-duration: 300ms;
}
.navegacion__enlace:hover, .navegacion__enlace.activo {
  border-style: solid;
  border: 100rem;
  border-radius: 2rem;
  background-color: #052bef;
  color: #FFFFF0;
}

.logo__nombre {
  color: gray;
  font-size: 3rem;
}
@media (min-width: 768px) {
  .logo__nombre {
    padding-bottom: 0;
  }
}

.logo__ag {
  font-family: "Beau Rivage", cursive;
  color: #052bef;
}

.header {
  background: rgba(0, 0, 0, 0.6) url(../img/principal2.jpg);
  height: 60rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  /**  codigo para dar oscuridad a una imagen**/
  background-blend-mode: darken;
}

.header__texto {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #FFFFF0;
  text-align: center;
}

.instalaciones-electricas__imagen {
  padding-top: 5rem;
}

.entrada {
  background-color: #f8f8f8;
  padding-top: 2rem;
  padding-right: 1rem;
  padding-left: 1rem;
}

/* media de instalaciones electricas*/
@media (min-width: 768px) {
  .entrada {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 8rem;
  }
}
.paneles {
  background-color: #f8f8f8;
}
.paneles .paneles__imagen {
  margin: auto;
}

.paneles-solares__imagen {
  padding-top: 5rem;
}

@media (min-width: 768px) {
  .paneles {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
}
.planos-articulo {
  background-color: #f8f8f8;
  padding-top: 2rem;
  padding-right: 1rem;
  padding-left: 1rem;
}

.planos-electricos__imagen {
  padding-top: 5rem;
}

@media (min-width: 768px) {
  .planos-articulo {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 3rem;
  }
  .planos-articulo p {
    padding-top: 0rem;
  }
}
.whatsapp {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  z-index: 30;
  width: 5rem;
  transition-property: width;
  transition-duration: 300ms;
}
.whatsapp:hover {
  cursor: pointer;
  width: 7rem;
}

.pie-pagina {
  background-color: #000;
  margin-top: 5rem;
}

.pie-pagina__contenedor {
  padding-top: 5rem;
  padding-bottom: 3rem;
  text-align: center;
}

@media (min-width: 768px) {
  .pie-pagina__contenedor {
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
  }
}
.pie-pagina__contactos {
  color: #FFFFF0;
}

.pie-pagina__contactos h4 {
  font-size: 3rem;
  font-weight: bold;
}

.pie-pagina__horarios {
  color: #FFFFF0;
}

.pie-pagina__horarios h4 {
  font-size: 3rem;
  font-weight: bold;
}

.logo__pie {
  padding-left: 8.5rem;
}

.logo__pie-pagina {
  color: #BFBFBA;
  margin-bottom: 2rem;
}

.icon {
  width: 2.5rem;
}

@media (min-width: 768px) {
  .pie-pagina__contenedor {
    text-align: unset;
  }
}
.nosotros {
  background: rgba(0, 0, 0, 0.6) url(../img/nosotros.webp);
  height: 10rem;
  background-size: cover;
  background-position: center center;
  background-blend-mode: darken;
}
.nosotros h2 {
  display: flex;
  color: #FFFFF0;
  justify-content: center;
  margin: 0;
  padding-top: 2rem;
}

.contenedor-contenido {
  background-color: #f8f8f8;
  margin-top: 2rem;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 1rem;
}
.contenedor-contenido p {
  padding-left: 1rem;
}

@media (min-width: 768px) {
  .contenedor-contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  .contenedor-contenido img {
    padding: 1rem;
  }
}
.campo svg {
  width: 10rem;
}

.contenedor-iconos {
  display: flex;
  flex-direction: column;
}
.contenedor-iconos h3 {
  text-align: center;
}
.contenedor-iconos .campo {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-iconos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 2rem;
    text-align: center;
    column-gap: 3rem;
  }
  .contenedor-iconos h3 {
    text-transform: uppercase;
  }
}

.servicios-electricos {
  display: flex;
  flex-direction: column;
  margin: 5rem 2rem 0 2rem;
}
.servicios-electricos h3 {
  text-align: center;
  margin: 2rem 0 2rem 0;
}
.servicios-electricos img {
  padding-top: 2rem;
}
.servicios-electricos p {
  margin-top: 0;
}
@media (min-width: 768px) {
  .servicios-electricos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 2rem;
    background-color: #f8f8f8;
  }
}

.servicios-header {
  background: rgba(0, 0, 0, 0.6) url(../img/servicios.jpg);
  height: 10rem;
  background-size: cover;
  background-position: center center;
  background-blend-mode: darken;
}
.servicios-header h2 {
  display: flex;
  color: #FFFFF0;
  justify-content: center;
  margin: 0;
  padding-top: 2rem;
}

.header-contacto {
  background: rgba(0, 0, 0, 0.6) url(../img/contacto.jpg);
  height: 10rem;
  background-size: cover;
  background-position: center center;
  background-blend-mode: darken;
}
.header-contacto h2 {
  display: flex;
  color: #FFFFF0;
  justify-content: center;
  margin: 0;
  padding-top: 2rem;
}

.formulario {
  display: flex;
  flex-direction: column;
}
.formulario .contenedor-contactenos {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
}
.formulario .contenedor-contactenos svg {
  width: 5rem;
}
.formulario .contenedor-contactenos h3 {
  margin: 0;
}
.formulario blockquote {
  border-style: solid;
  border-width: 0 0 0 0.5rem;
  border-color: #052bef;
  background-color: #f8f8f8;
  color: gray;
  margin-left: 0;
}
.formulario blockquote p {
  padding-left: 1rem;
}
.formulario .iconos-contacto {
  display: flex;
  text-align: center;
}
.formulario .iconos-contacto svg {
  width: 5rem;
}
@media (min-width: 768px) {
  .formulario {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}

.formulario-contacto {
  margin-top: 5rem;
}
.formulario-contacto fieldset {
  border-style: solid;
  border-color: #FFFFF0;
  border-radius: 1rem;
  background-color: #f8f8f8;
  text-align: center;
}
.formulario-contacto fieldset input {
  border-style: none;
  border-radius: 0.5rem;
  width: 90%;
  height: 4rem;
  margin: 1rem;
}
.formulario-contacto fieldset textarea {
  border-style: none;
  border-radius: 0.5rem;
  overflow: auto;
  resize: vertical;
  width: 90%;
  margin: 1rem;
  height: 10rem;
}
.formulario-contacto fieldset input[type=submit] {
  cursor: pointer;
  background-color: #052bef;
  color: #FFFFF0;
  transition-property: background-color;
  transition-duration: 500ms;
  width: 30%;
}
.formulario-contacto fieldset input[type=submit]:hover {
  background-color: #021059;
}
.formulario-contacto p {
  color: #000;
  line-height: 2rem;
  font-size: 1rem;
  text-align: justify;
}

.alerta .exito {
  background-color: #329f00;
  color: #FFFFF0;
  text-align: center;
  font-size: 15px;
  margin: 0 1rem 0 1rem;
  padding: 0.5rem;
}
.alerta .error {
  background-color: #cb0000;
  color: #FFFFF0;
  text-align: center;
  font-size: 15px;
  margin: 0 1rem 0 1rem;
  padding: 0.5rem;
}

.servicio {
  background: rgba(0, 0, 0, 0.6);
  background-size: cover;
  background-position: center center;
  background-blend-mode: darken;
}

.servicio-contenedor {
  display: flex;
  flex-direction: column;
  text-align: center;
}
.servicio-contenedor li {
  text-align: start;
}
.servicio-contenedor fieldset {
  background-color: rgba(255, 255, 240, 0.25);
  border-style: unset;
  margin-bottom: 3rem;
}
.servicio-contenedor fieldset input {
  background-color: rgba(255, 255, 240, 0.8);
}
.servicio-contenedor fieldset textarea {
  background-color: rgba(255, 255, 240, 0.8);
}
.servicio-contenedor fieldset p {
  color: #FFFFF0;
}
.servicio-contenedor fieldset h3 {
  color: #FFFFF0;
}
@media (min-width: 768px) {
  .servicio-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .servicio-contenedor fieldset {
    width: 200%;
  }
  .servicio-contenedor .formulario-servicio {
    width: 50%;
    padding-bottom: 5rem;
  }
}
.servicio-contenedor .descripcion-servicio {
  color: #FFFFF0;
  font-size: 2rem;
}
.servicio-contenedor .descripcion-servicio p {
  font-size: 2rem;
}

.servicio-contenido {
  text-align: center;
  color: #3c3c3c;
}
.servicio-contenido h3 {
  margin: 4rem 0 0 0;
  color: #052bef;
}
.servicio-contenido h4 {
  font-size: large;
  margin: 1rem 0 1rem 0;
  color: #052bef;
}

.contenedor-caracteristicas {
  text-align: left;
}

.caracteristicas-servicios {
  display: flex;
  flex-direction: column;
}

.caracteristicas-servicio {
  display: flex;
  align-items: center;
  padding: 1rem 0 1rem 0;
}
.caracteristicas-servicio h5 {
  margin: 0;
  font-size: medium;
  color: #052bef;
}
.caracteristicas-servicio svg {
  width: 5rem;
  margin-right: 3rem;
}
.caracteristicas-servicio p {
  margin: 0;
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login h1 {
  margin: 0;
  margin-top: 3rem;
  font-size: 4rem;
}

.formulario-login {
  text-align: center;
  margin-top: 5rem;
  width: 50rem;
}
.formulario-login fieldset {
  border-style: solid;
  border-color: #FFFFF0;
  border-radius: 1rem;
  background-color: #f8f8f8;
  text-align: start;
  margin-left: 7rem;
  margin-right: 7rem;
}
@media (min-width: 768px) {
  .formulario-login fieldset {
    margin: 0;
  }
}
.formulario-login fieldset input {
  border-style: none;
  border-radius: 0.5rem;
  width: 100%;
  height: 4rem;
  margin: 1rem;
  margin-left: 0;
}
.formulario-login input[type=submit] {
  cursor: pointer;
  background-color: #052bef;
  color: #FFFFF0;
  padding: 1rem;
  border-color: rgba(255, 255, 240, 0);
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 500ms;
  margin-top: 1rem;
}
.formulario-login input[type=submit]:hover {
  background-color: #021059;
}

.session-error {
  background-color: #cb0000;
  color: #FFFFF0;
  width: 50rem;
  text-align: center;
  font-weight: bold;
  padding: 0.5rem;
  margin: 0.5rem;
}

.admin-header {
  background: rgba(0, 0, 0, 0.6) url(../img/login.jpg);
  height: 15rem;
  background-size: cover;
  background-position: center center;
  background-blend-mode: darken;
}
.admin-header h1 {
  margin: 0;
  color: #FFFFF0;
  text-align: center;
  padding-top: 2rem;
}
@media (min-width: 768px) {
  .admin-header {
    height: 10rem;
  }
}

.crear-servicio {
  margin: 2rem 0 2rem 2rem;
}
.crear-servicio a {
  border-radius: 0.5rem;
  padding: 2rem;
  color: #FFFFF0;
  font-weight: bold;
  transition-property: background-color;
  transition-duration: 300ms;
  width: 15%;
  background-color: #42d200;
