@use 'base/mixins' as m;
@use 'base/variables' as v;
.boton {
    background-color: v.$colorAg;
    color: v.$blanco;
    padding: 1rem;
    font-weight: bold;
    text-decoration: none;
    display: flex;
    text-align: center;
    flex-direction: column;
    border-radius: .5rem;
    margin-bottom: 1rem;
    transition-property: background-color;
    transition-duration: 300ms;
    &:hover{
        background-color: darken(v.$colorAg, 20%);
    }
}