@use 'base/mixins' as m;
@use 'base/variables' as v;

.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    
    
    h1{
        margin: 0;
        margin-top: 3rem;
        font-size: 4rem;
    }
}
.formulario-login{
    text-align: center;
    margin-top: 5rem;
    width: 50rem;
    
    fieldset{
     border-style: solid;
     border-color: v.$blanco;
     border-radius: 1rem;
     background-color: v.$grisIntermedio;
     text-align: start;
     margin-left: 7rem;
     margin-right: 7rem;
     @include m.tablet{
        margin: 0;
     }
     input{
         border-style:none ;
         border-radius: .5rem;
         width: 100%;
         height: 4rem;
         margin: 1rem;
         margin-left: 0;
        
     }
    
    
    }
    input[type="submit"]{
        cursor: pointer;
        background-color: v.$colorAg;
        color: v.$blanco;
        padding: 1rem;
        border-color: rgba(v.$blanco, 0);
        border-radius: .5rem;
        transition-property: background-color;
        transition-duration: 500ms;
        margin-top: 1rem;
       &:hover{
        background-color: darken(v.$colorAg, 30%);
       }
    }
}
.session-error{
    background-color: v.$rojo;
    color: v.$blanco;
    width: 50rem;
    text-align: center;
    font-weight: bold;
    padding: .5rem;
    margin: .5rem;
}